import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import Card from "../components/UI/Card";
import { getUserDetails } from "../redux/actions/userActions";
import styles from "./styles.module.css";
import {
  GiCheckMark,
  GiChart,
  GiChainedArrowHeads,
  GiChessKing,
} from "react-icons/gi";
import Chart from "../components/UI/Chart";
import { fetchMyTeam } from "../redux/actions/teamActions";
import { Box, Center, Badge } from "@chakra-ui/react";

const Dashboard = ({ history }) => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const myTeam = useSelector((state) => state.myTeam);
  const { team } = myTeam;
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getUserDetails());
    dispatch(fetchMyTeam());
  }, [dispatch]);
  if (!user) {
    history.push("/");
  }
  return (
    <div className={styles.mainApp}>
      <Navigation />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Dashboard" />
          <br />
          <div className={styles.cardGrid}>
            <Card color={styles.purple} name="My Appraisal Score" score="10">
              <GiCheckMark />
            </Card>
            <Card color={styles.cyan} name="Manager's Rating" score="10">
              <GiChessKing />
            </Card>
            <Card color={styles.red} name="Overall Rating" score="10">
              <GiChart />
            </Card>
            <Card color={styles.gold} name="HR's Calibration" score="10">
              <GiChainedArrowHeads />
            </Card>
          </div>
        </div>
        <div className={styles.contentSm}>
          <div className={styles.chartGrid}>
            <div className={styles.box}>
              <Chart />
            </div>
            <div className={`${styles.box} ${styles.scroll}`}>
              <p>My Team</p>
              {team &&
                team.map((item) => (
                  <>
                    <Box
                      maxW="sm"
                      borderWidth="1px"
                      borderRadius="lg"
                      overflow="hidden"
                      mb="2"
                    >
                      <div className={styles.teamGrid}>
                        <Center>
                          <img
                            src={`/uploads/${user ? user.photo : ""}`}
                            alt={user && user.firstname}
                            style={{
                              width: "50px",
                              height: "50px",
                              borderRadius: "50%",
                            }}
                          />
                        </Center>

                        <Box p="4">
                          <Box alignItems="baseline">
                            <Box
                              color="gray.500"
                              fontWeight="semibold"
                              letterSpacing="wide"
                              fontSize="h4"
                              textTransform="uppercase"
                            >
                              {item.firstname + " " + item.lastname}{" "}
                              {item.role === "Manager" && (
                                <Badge ml="1" colorScheme="green">
                                  {item.role}
                                </Badge>
                              )}
                            </Box>

                            <Box color="gray.500" fontSize="xs">
                              {item.position}
                            </Box>
                            <Box color="gray.500" fontSize="xs">
                              {item.email}
                            </Box>
                            <Box color="gray.500" fontSize="xs">
                              <Badge colorScheme="green">CUG:</Badge>
                              {item.cug}
                            </Box>
                            <Box color="gray.500" fontSize="xs">
                              <Badge colorScheme="red">Mobile:</Badge>
                              {item.mobile}
                            </Box>
                          </Box>
                        </Box>
                      </div>
                    </Box>
                  </>
                ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
